@import url(https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,500,500i,600,600i,800,800i&display=swap);
@import url(https://cdn.web.wsu.edu/designsystem/1.x/wsu-icons/dist/wsu-icons.bundle.css);
html{font-family:"Montserrat",sans-serif;font-size:16px;line-height:1.6;color:#333}h1,h2,h3,h4,h5,h6{-webkit-transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1);transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1)}p{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}img{width:100%}.wsu-u-visually-hidden{position:absolute;overflow:hidden;clip:rect(1px, 1px, 1px, 1px);width:1px;height:1px;margin:-1px;padding:0;-webkit-clip-path:inset(50%);clip-path:inset(50%)}.wsu-u-spacer{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;height:200px;margin:20px 0;padding:0 40px;background:-o-linear-gradient(285deg, #fff, #e6e6e6);background:linear-gradient(-195deg, #fff, #e6e6e6)}.wsu-u-title{font-family:monospace;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.wsu-u-toc{display:block;max-width:400px;margin:15px auto;padding:10px 20px;color:#fff;background:-o-linear-gradient(285deg, gray, #4d4d4d);background:linear-gradient(-195deg, gray, #4d4d4d);font-size:14px;font-weight:300;line-height:1.5;-webkit-transition:-webkit-box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);transition:-webkit-box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);transition:box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);transition:box-shadow 300ms cubic-bezier(0, 0, 0.03, 1), -webkit-box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);-webkit-box-shadow:0 8px 10px 1px rgba(0,0,0,0.03),0 3px 14px 2px rgba(0,0,0,0.03),0 4px 15px 0 rgba(0,0,0,0.02);box-shadow:0 8px 10px 1px rgba(0,0,0,0.03),0 3px 14px 2px rgba(0,0,0,0.03),0 4px 15px 0 rgba(0,0,0,0.02)}.wsu-u-toc p{margin:0}.wsu-u-toc a{-webkit-transition:color 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:color 300ms cubic-bezier(0, 0, 0.03, 1);transition:color 300ms cubic-bezier(0, 0, 0.03, 1);color:#fff}.wsu-u-toc a:hover{color:#ccc}.wsu-u-requirements{width:400px}.wsu-u-pad--none{padding-top:0;padding-right:0;padding-bottom:0;padding-left:0}.wsu-u-pad--less{padding-top:0;padding-right:0;padding-bottom:0;padding-left:0}.wsu-u-pad--more{padding-top:0;padding-right:0;padding-bottom:0;padding-left:0}.wsu-u-pad-bottom--none{padding-bottom:0}.wsu-u-pad-top--none{padding-top:0}.wsu-u-pad-left--none{padding-left:0}.wsu-u-pad-right--none{padding-right:0}.wsu-u-textalign--left{text-align:left}.wsu-u-textalign--right{text-align:right}.wsu-u-textalign--center{text-align:center}.wsu-c-icon-container{max-width:800px;margin:30px auto}.wsu-icon{display:inline-block;padding:20px;-webkit-transition:color 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:color 300ms cubic-bezier(0, 0, 0.03, 1);transition:color 300ms cubic-bezier(0, 0, 0.03, 1)}.wsu-icon:hover{color:#CA1237}

